.grid-container {
    width: 100%;
    height: 100%;
    display: flex;
}

.gridCol-container {
    display: flex;
    width: 100%;
    height: 100%;
}

.gridColTriple {
    width: 33.33%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.gridColDouble {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.gridColSingle {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}